/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* Style for body background */
body, html {
  height: 100%;
  font-family: 'Roboto', sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}
.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bg { 
  /* The image used */
  background-image: url(/Water_the_Plants_react/static/media/9290.51ac4a0d.jpg);

  /* Full height */
  height: 100vh; 

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

/* Changing styles of collapsible content */

.Collapsible__trigger {
  font-size: 2rem;
  color: #94c9ab;
  font-weight: 500;
  margin-bottom: 15px;
  cursor: pointer;
  display: inline-block;
}

.is-open {
  font-size: 2rem;
  color: #94c9ab;
  font-weight: 500;
  margin-bottom: 15px;
  cursor: pointer;
}

.Collapsible__contentInner a {
  color: #d29596;
  margin-bottom: 8px;
  font-size: 1.7rem;
  display: inline-block;
}


/*Changing styles of form */
.ui.form .field>label {
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #d29596;
  font-weight: 400;
}

.ui.form .field.mg-top{
  margin-top: 20px;
}

.ui.form .field>input {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.ui.form .field>textarea {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.ui .button {
  background: none;
  border: 2px solid #d29596;
  font-size: 1.5rem;
  text-transform: uppercase;

}

.ui .button:hover {
  background: none;
}


/*Changing styles of search bar*/
.centered-width {
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-content: baseline;
}

.centered {
  margin-top: 20px !important;
  width: 80%;
}

/*Changing styles of button */
.addplant {
  display: inline-block;
  height: 40px;
  padding: 0;
  margin-top: 20px;
  padding-left: 15px;
  padding-right: 15px;
  background: #94c9ab;
  border: none;
  color: #fff;
  font-size:  1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  padding-top: 13px;
  padding-bottom: 2px;
  border-radius: 3px;
  cursor: pointer;
}

.addplant:hover{
  background: #76a088;
  color: #fff;
}
